import React, { Component } from "react";
import CardNota from "../CardNota";
import "./estilo.css";
class ListaDeNotas extends Component {

  constructor() {
    super();
    this.state = {};
    this.referencia = this.criarTela.bind(this);
  }

  componentDidMount() {
    this.props.notas._inscrever(this.referencia)
  }

  componentWillUnmount() {
    this.props.notas._desinscrever(this.referencia);
  }

  criarTela(notas) {
    this.setState({...this.state, notas})
  }
 
  render() {
    return (
      <ul className="lista-notas">
        {this.props.notas.notas.map((nota, index) => {
          return (
            <li className="lista-notas_item" key={index}>
              
              <CardNota 
              indice={index}
              deletar={this.props.deletar}
              titulo={nota.titulo} 
              texto={nota.texto}
              categoria={nota.categoria}
              />
            </li>
          );
        })}
      </ul>
    );
  }
}

export default ListaDeNotas;
